    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes animationVideoDown {
  from {
    -webkit-transform: translateY(-110%);
    transform: translateY(-110%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@-webkit-keyframes animationVideoUp {
  from {
