  width: calc(100% - 240px);
  height: 100%;
  position: relative;
}
.popup-zoom .img-zoom {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.popup-zoom .wrap-caption-zoom {
  width: 240px;
  height: 100%;
  padding: 110px 20px 20px 20px;
  color: #e0e0e0;
  border-left: 1px solid #4f4f4f;
  font-size: 15px;
  line-height: 160%;
}
.popup-zoom .wrap-caption-zoom p {
  margin-bottom: 20px;
}
.popup-zoom.no-caption .wrap-img-zoom {
  width: 100%;
}
.popup-zoom.no-caption .wrap-caption-zoom {
  display: none;
}
.popup-zoom .inner-caption {
  max-height: calc(100% - 110px);
  overflow: hidden;
}
.popup-zoom .inner-caption .ss-content {
  padding-right: 20px;
}
.popup-zoom .inner-caption .ss-scroll {
  background: #424242;
}
.show-zoom .popup-zoom {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  -webkit-transform: scale(1);
}
